* {
	box-sizing: border-box;
	outline: 0;
	margin: 0 {
  padding: 0;
  font-size: 16px; }
	color: $color_text;

	&:after,
	&:before {
		box-sizing: border-box; } }

html {
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
	font-size: $base-font-size; }

body {
	width: 100%;
	height: 100%;
	margin: 0;
	padding: 0;
	padding-top: 2em;
	background-color: #fff;
	overflow-x: hidden;
	font: {
		family: $main-font-family;
		size: 1rem; }
	line-height: $base-line-height;
	color: #212121;
	// background-position-y: 1%
	@include flex($f_direction: column, $justify: center); }
.body_main {
	background: url(/images/bg_top-min-100.jpg) no-repeat;
	background-size: contain;
	background-position-y: .5%; }
.body_page {
	background: url(/src/images/bg_page.png) no-repeat;
	position: relative;
	&:before {
		@include pseudo-element($top: 7%, $left: 10%, $right: unset);
		width: 10em;
		height: 10em;
		background: url('/src/images/petal1.png') no-repeat center;
		background-size: contain;
		z-index: -1; }
	.state_sign {
		&:before {
			display: none; } } }

ol,
ul {
	margin: 0;
	padding: 0;
	list-style-type: none;
	font-weight: inherit; }

a {
	outline: 0;
	border: 0;
	color: inherit;
	text-decoration: none;

	&:hover,
	&:focus {
		text-decoration: underline; } }

img {
	vertical-align: middle;
	border: 0; }

table {
	background-color: transparent;
	border-collapse: collapse; }

.conteiner {
  @include flex($f_direction: column);
  max-width: 72.8125em;
  width: 90%; }

.wrap_main {
	@include flex($align: flex-start);
	margin-top: 4%;
	margin: 0 auto;
	width: 100%; }
